  margin-left: 20px;
  display: flex;
  flex-direction: column;
}

.font-upper-balance {
  color: #212529;
  font-size: 16px;
  font-weight: 700;
}

.font-down-balance {
  color: #212529;
  font-size: 13px;
}

.content-background {
  background-color: rgb(255, 255, 255);
  padding: 10px 15px;
  border-radius: 7px;
  margin-top: 10px;
}

.activity-title {
  padding-top: 0;
}
.activity-title p {
  color: #212529;
  font-size: 16px;
  font-weight: 500;
  line-height: 1.2;
}

.activity-account {
  border-bottom: 1px solid #e6e6e6;
}
.activity-account .account-button {
  background-color: #aac9ff;
  color: #1877f2;
  font-weight: 700;
}

.activity-id {
  display: flex;
  justify-content: space-between;
}
.activity-id .id-threshold {
  display: flex;
}
.activity-id .id-threshold .threshold-data {
  text-align: right;
  margin-right: 15px;
}
.activity-id .id-threshold .threshold-button {
  background-color: #aac9ff;
  color: rgba(51, 132, 237, 0.5803921569);
  font-weight: 700;
  padding: 0 15px;
}

.background-paid {
  background-color: rgb(218, 242, 194);
  color: rgb(57, 115, 0);
  border-radius: 10px;
  text-align: center;
  font-size: 9px;
  padding: 2px 7px;
